.Menu {
	width: 40px;
	height: 100%;
	display: flex;
	flex-flow: column;
	justify-content: space-around;
	align-items: center;
	padding: 10px 0;
	box-sizing: border-box;
	background-color: #5c9210;
	color: white;
	font-size: 32px;
}

.Menu div {
	width: 90%;
	height: 3px;
	background-color: white;
}

@media (min-width: 500px) {
	.Menu {
		display: none;
	}
}
